import React from "react";
import { Link } from "react-router-dom";

export default function Card({ url, id, name, description, ...props }) {
  return (
    <div className={`card card--${id}`}>
      <h2>{name}</h2>
      <p>{description}</p>
      <Link>
        <img
          className="card__img"
          src={process.env.PUBLIC_URL + url}
          alt="project "
        />
      </Link>
    </div>
  );
}
